$activeColor:#1890ff;

.popular {
  .popular-menu {
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -moz-box-pack: center;
     -ms-flex-pack: center;
         justify-content: center;
    font-size: 18Px;

    .popular-menu-item {
      margin: 0 0.16rem;
      cursor: pointer;
      color: rgba(0, 0, 0, .65);

      &:hover {
        color: $activeColor;
      }
    }

    .active {
      color: $activeColor;
    }

  }

  .popular-content {
    padding: 0 0.32rem;
    width: 100%;
    display: -moz-box;
    display: -ms-flexbox;
    display: flex;
    -moz-box-align: stretch;
     -ms-flex-align: stretch;
         align-items: stretch;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;

    .popular-content-item {
      -moz-box-sizing: border-box;
           box-sizing: border-box;
      width: 23%;
      min-width: 135Px;
      padding: 0.24rem;
      margin-top: 0.1rem;
      margin-bottom: 0.1rem;
      border-radius: 0.08rem;
      background-color: rgba(0, 0, 0, 0.08);
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      -moz-box-orient: vertical;
      -moz-box-direction: normal;
       -ms-flex-direction: column;
           flex-direction: column;
      -moz-box-align: center;
       -ms-flex-align: center;
           align-items: center;

      .item-index {
        font-weight: bold;
        font-size: 0.12rem;
        text-align: center;
      }

      .item-image-lazy {
        width: 100%;
        display: -moz-box;
        display: -ms-flexbox;
        display: flex;
        -moz-box-pack: center;
         -ms-flex-pack: center;
             justify-content: center;

        .item-image {
          margin-top: 0.1rem;
          width: 50%;
        }
      }

      .item-fullName {
        display: inline-block;
        margin-top: 0.1rem;
        font-size: 0.08rem;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        line-clamp: 2;
        text-decoration: none;
        word-break: break-all;
        color: rgba(0, 0, 0, .65);
        font-weight: bold;

        &:hover {
          color: $activeColor;
        }
      }

      .item-text {
        margin-top: 0.1rem;
        font-size: 0.08rem;
        width: 100%;

        .item-text-child {
          width: 100%;
          display: -moz-box;
          display: -ms-flexbox;
          display: flex;
          margin-bottom: 0.04rem;

          .icon {
            margin-right: 0.03rem;

            .icon-font {
              font-size: 12Px;
              display: inline-block;
              margin-right: 0.01rem;
            }

            .icon-account {
              @extend .icon-font;
              color: #f5c27f;
            }

            .icon-start {
              @extend .icon-font;
              color: #f9d849;
            }

            .icon-fork {
              @extend .icon-font;
              color: #90c1f0;
            }

            .icon-issues {
              @extend .icon-font;
              color: #e38f95;
            }
          }

          .item-text-info {
            word-break: break-all;
          }

          .item-name {
            font-size: 0.08rem;
            color: rgba(0, 0, 0, .65);

            &:hover {
              color: $activeColor;
            }
          }
        }
      }
    }
  }

  .loading {
    width: 100%;
    min-height: 80Px;
  }
}